import React from 'react';
import './Spinner.scss';

interface Props {
	/**
	 *    .
	 * */
	className?: string;
	/**
	 *   .
	 * */
	status?: true;
	/**
	 *      .
	 * */
	size?: number;
}

const Spinner: React.FC<Props> = ({className, status, size = 28}) => {
  return (
    <div className={'spinner la-ball-pulse ' + (className || '')} style={{ height: size }}>
      <div />
      <div />
      <div />
    </div>
  );
}

export default Spinner;

